<template>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <span>店铺信息</span>
    </div>
    <!-- 表单 -->
    <el-form :model="form">
      <el-form-item label="店铺ID" :label-width="formLabelWidth">
        <el-input disabled v-model="form.sid" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="店铺名称" prop="sname" :label-width="formLabelWidth">
        <el-input disabled v-model="form.sname" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="所属食堂" :label-width="formLabelWidth">
        <el-button type="primary" disabled>{{ form.roomName }}</el-button>
      </el-form-item>
      <el-form-item
        label="店铺位置"
        prop="location"
        :label-width="formLabelWidth"
      >
        <el-input
          disabled
          v-model="form.location"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item label="管理员账号" :label-width="formLabelWidth">
        <el-input disabled v-model="form.manager" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="店铺电话" prop="tel" :label-width="formLabelWidth">
        <el-input disabled v-model="form.tel" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="店铺状态" :label-width="formLabelWidth">
        <el-button
          type="success"
          v-if="form.status == 0"
          @click="updateShopFn(1)"
          title="点击停业整顿"
          >正在营业</el-button
        >
        <el-button
          v-else
          type="info"
          @click="updateShopFn(0)"
          title="点击开始营业"
          >停业整顿</el-button
        >
      </el-form-item>
    </el-form>
  </el-card>
</template>

<script>
import { getRoomNameListAPI, updateShopAPI } from "@/api";
export default {
  name: "ShopInfo",
  data() {
    return {
      form: {},
      // 食堂列表
      roomList: [
        {
          rid: 1,
          rname: "一食堂",
        },
        {
          rid: 2,
          rname: "二食堂",
        },
        {
          rid: 3,
          rname: "三食堂",
        },
      ],
      // 用于控制表单label的宽
      formLabelWidth: "120px",
    };
  },
  //   ==============================================以下是method方法==============================================
  methods: {
    // 获取食堂列表
    async getRoomList() {
      const { data: res } = await getRoomNameListAPI();
      if (res.code !== 0) return this.$message.error("获取食堂列表失败！");
      this.roomList = res.data;
      this.form = this.$store.state.shopInfo;
      this.roomList.forEach((room) => {
        if (room.rid == this.form.rid) this.form.roomName = room.rname;
      });
    },
    // 店铺状态改变
    async updateShopFn(status) {
      const { data: res } = await updateShopAPI({ ...this.form, status });
      if (res.code != 0) return this, this.$message.error("店铺状态修改失败！");
      this.$message.success("店铺状态修改成功！");
      this.$store.dispatch("initShopInfo");
      this.getRoomList();
      this.$router.push("/serve_or_not");
    },
  },
  //   ==============================================以下是mounted方法==============================================
  mounted() {
    this.getRoomList();
  },
};
</script>

<style lang="less" scoped>
.el-form {
  width: 500px;
}
</style>
